import React from 'react';
import './ImageLinkForm.css';

const ImageLinkForm = ({ onInputChange, onButtonSubmit }) => {
	return (
		<div>	
			<p className='f3'>
				{'This Magic Brain will detect your faces in your picture. Git it now...'}
			</p>
			<div className='center'>
				<div className='pa4 form center br3 shadow-5'>
					<input className='f4 pa2 w-70 center' type='text' onChange = {onInputChange} />
					<button onClick = {onButtonSubmit} className='w-30 f4 grow ph2 pv2 link dib white bg-light-purple'> Detect</button>
				</div>
			</div>
		</div>
	);
}

export default ImageLinkForm;